import React, { Component } from 'react';
import workout from '../images/workOutFlow.jpg';
import Projects from './Projects';
import Team from './Team';
import Features from './Features';

export default class Jumbotron extends Component {
  render() {
    return (
      <div>

          
                <div className="card card-image" style={{backgroundImage: `url(${workout})`}} >
                <div className="text-white text-center rgba-stylish-strong py-5 px-4">
                    <div className="py-5">

                   
                    <h5 className="h5 orange-text"><i className="fa fa-camera-retro"></i> Workout Flow</h5>
                    <h2 className="card-title h2 my-4 py-2">Modern Workouts for the Modern Athlete</h2>
                    <p className="mb-4 pb-2 px-md-5 mx-md-5">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Pariatur obcaecati vero aliquid libero doloribus ad, unde tempora maiores, ullam, modi qui quidem minima debitis perferendis vitae cumque et quo impedit.</p>
                    <a className="btn success-color"><i className="fa fa-clone left"></i> View Workouts</a>

                    </div>
                </div>
                </div>

                <div className='container'>

                <Projects />

                <Team />

                <Features />


                </div>

        
      </div>
    )
  }
}



