import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Navigation extends Component {
  render() {
    return (
      <div>

         
<nav className="navbar navbar-expand-lg navbar-dark elegant-color">

  
  <a className="navbar-brand text-white" ><i className="fa fa-dashboard mr-3" />Workout Flow</a>

  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#basicExampleNav"
    aria-controls="basicExampleNav" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

 
  <div className="collapse navbar-collapse" id="basicExampleNav">

    
    <ul className="navbar-nav ml-auto">
      <li className="nav-item">
      <Link to="/" className="nav-link">
                <i className="fa fa-home" /> Home
              </Link>
      </li>
      <li className="nav-item">
      <Link to="/workout" className="nav-link">
                <i className="fa fa-user" /> Workout
              </Link>
      </li>
      <li className="nav-item">
      <Link to="/contact" className="nav-link">
                <i className="fa fa-send" /> Contact
              </Link>
      </li>

    

    </ul>

    </div>
   

    
 

</nav>

        
      </div>
    )
  }
}



