import React, { Component } from 'react'
import workout from '../images/workOutFlow.jpg';

export default class Workouts extends Component {
  render() {
    return (
      <div>

<div className='container pt-5 pb-5'>

            <div className="card card-image" style={{backgroundImage: `url(${workout})`}} >

 
<div className="text-white text-center d-flex align-items-center rgba-black-strong py-5 px-4">
  <div>
    <h5 className="pink-text"><i className="fa fa-pie-chart"></i> Shoulders</h5>
    <h3 className="card-title pt-2"><strong>15 Minutes</strong></h3>
    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Repellat fugiat, laboriosam, voluptatem,
      optio vero odio nam sit officia accusamus minus error nisi architecto nulla ipsum dignissimos.
      Odit sed qui, dolorum!.</p>
    <a className="btn btn-pink"><i className="fa fa-clone left"></i> View workout</a>
  </div>
</div>

</div>



</div>
          


        
      </div>
    )
  }
}



