import React, { Component } from 'react'

export default class Contact extends Component {
  render() {
    return (
      <div>


          <div className='container'>

                
<section class="contact-section my-5">


  <div class="card">

   
    <div class="row">

      <div class="col-lg-8">

        <div class="card-body form">

          
          <h3 class="mt-4"><i class="fa fa-envelope pr-2"></i>Write to us:</h3>

         
          <div class="row">

            
            <div class="col-md-6">
              <div class="md-form mb-0">
                <input type="text" id="form-contact-name" class="form-control" />
                <label for="form-contact-name" class="">Your name</label>
              </div>
            </div>
            

            
            <div class="col-md-6">
              <div class="md-form mb-0">
                <input type="text" id="form-contact-email" class="form-control" />
                <label for="form-contact-email" class="">Your email</label>
              </div>
            </div>
            

          </div>
         

         
          <div class="row">

            
            <div class="col-md-6">
              <div class="md-form mb-0">
                <input type="text" id="form-contact-phone" class="form-control" />
                <label for="form-contact-phone" class="">Your phone</label>
              </div>
            </div>
            

            
            <div class="col-md-6">
              <div class="md-form mb-0">
                <input type="text" id="form-contact-company" class="form-control" />
                <label for="form-contact-company" class="">Your company</label>
              </div>
            </div>
            

          </div>
         

         
          <div class="row">

            
            <div class="col-md-12">
              <div class="md-form mb-0">
                <textarea type="text" id="form-contact-message" class="form-control md-textarea" rows="3"></textarea>
                <label for="form-contact-message">Your message</label>
                <a class="btn-floating btn-lg elegant-color">
                  <i class="fa fa-send-o"></i>
                </a>
              </div>
            </div>
            

          </div>
         

        </div>

      </div>

      <div class="col-lg-4">

        <div class="card-body elegant-color text-center h-100 white-text">

          <h3 class="my-4 pb-2">Contact information</h3>
          <ul class="text-lg-left list-unstyled ml-4">
            <li>
              <p><i class="fa fa-map-marker pr-2"></i>Utah</p>
            </li>
            <li>
              <p><i class="fa fa-phone pr-2"></i>+ 01 234 567 89</p>
            </li>
            <li>
              <p><i class="fa fa-envelope pr-2"></i>contact@example.com</p>
            </li>
          </ul>
          <hr class="hr-light my-4" />
          <ul class="list-inline text-center list-unstyled text-white">
            <li class="list-inline-item">
              <a class="p-2 fa-lg tw-ic">
                <i class="fa fa-twitter text-white"></i>
              </a>
            </li>
            <li class="list-inline-item">
              <a class="p-2 fa-lg li-ic">
                <i class="fa fa-linkedin text-white "> </i>
              </a>
            </li>
            <li class="list-inline-item">
              <a class="p-2 fa-lg ins-ic">
                <i class="fa fa-instagram text-white"> </i>
              </a>
            </li>
          </ul>

        </div>

      </div>

    </div>
   

  </div>
  

</section>

</div>

        
      </div>
    )
  }
}



